.mainContainer {
  display: flex;
  flex-flow: column;
  color: white;
}

.rowContainer {
  display: flex;
  flex-direction: row;
  column-gap: 1rem;
  justify-content: space-evenly;
}

.rowWrapContainer {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-evenly;
  column-gap: 1rem;
}

.columnContainer {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.resultNumber {
  font-size: 1.25rem;
  color: rgb(242, 224, 28);
}

.jobID {
  align-self: center;
}

.jobID input {
  text-align: center;
  width: 100%;
}
