.printSource {
  display: none;
}

body > .printSource {
  display: block;
  font-family: sans-serif;
}

.mainContainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.header {
  display: flex;
  flex-flow: row;
  justify-content: center;
  align-items: center;
  column-gap: 1rem;
}

.header img {
  height: 3rem;
  width: auto;
  margin: 0rem 0rem 0.5rem 0rem;
}

.section {
  display: flex;
  flex-flow: row wrap;
  border: solid black 0.2rem;
  justify-content: center;
  margin: 0.5rem;
  padding: 0.5rem;
}

.section h3 {
  padding: 0rem 1rem;
}

.fullRow {
  flex: 1 100%;
  display: flex;
  justify-content: center;
}

.timestamp {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
}

@media print {
  .printSource {
    display: flex;
  }
}
